
/*start footer*/
footer{
  padding: 15px;
  background: $blue;
  .foot-head{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 9px;
    border-bottom: 1px solid #336CBE;
    .foot-logo{
      img{
        width: 100px;
      }
    }
    .foot-social{
      ul{
        display: flex;
        align-items: center;
        li{
          margin-right: 10px;
          a{
            width: 30px;
            height: 30px;
            border: 1px solid #fff;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            background:#ffffff26;
            img{
              width: 15px;
            }
          }
        }
      }
    }
  }
}
.img-icon a img{
  width: 9px !important;
}
.foot-bod{
  padding-top: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  span{
    color: #fff;
    font-size: .9rem;
  }
  ul{
    display: flex;
    align-items: center;
    li{
      margin-right: 15px;
      a{
        color: #fff;
        font-size: .8rem;
      }
    }
  }
}
/*start footer*/