$font-main: 'Raleway', sans-serif;
$min-color:rgb(212, 211, 209);
$blue: #0C50B2;
$pink: #FF389F;
$pink-hover: #e0318c;
* {
  margin: 0;
  padding: 0;
  font-family: $font-main ;
  // border: 1px solid red;

}

body{
  background-color: #F0F3F7 !important;
}

p {
  margin: 0;

}

ul {
  list-style: none;
  margin-bottom: 0 !important;
}

ol,
ul,
h1,
h2,
h3,
h5,
h4,
h6 {
  margin: 0;
  padding: 0;
}
a{
  text-decoration: none !important;
}

// .container {
//   max-width: 1560px;
//   width: 92%; }
%unselect {
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
}
body {

  @media (max-width: 992px) {
    header{
      height: auto;
      position: relative;
      overflow: hidden;
    }
    .over-title h2{
      width: 80%;
    }
    .form-search .select-container{
      width: 100%;
    }
    .nav-info{
      display: none !important;
    }
    .form-search{
      height: auto;
      flex-direction: column;
    }
    .form-search-item{
      margin-bottom: 10px;
      width: 100%;
    }
    .button-item{
      width: 100%;
    }
    .icon-burg{
      display: block;
    }
    .layout{
      display: block;
    }
    .doc-item {
      margin-bottom: 20px;
      margin-top: 10px;
      padding: 10px;
      background: #eeeeee36;
      border-radius: 10px;
      box-shadow: 0px 0px 4px 1px #0000001a;
    }

    
      .is-result-date{
        width: 100%;
      }
      // .sliders-time{
      //   width: 100%;
      // }
    .innerform{
      padding-bottom: 20px;
    }

    .innerform .form-search{
      margin-top: 20px;
      margin-bottom: 0;
    }
    .innerform .form-search .form-search-item{
      width: 100%;
    }
    .img-about{
      text-align: center;
    }
    .details-user{
      max-width: 350px;
      margin: 20px auto;
    }
    .item-tap-one .contant-dr .item-s-result{
      margin-bottom: 0;
      padding: 15px 15px 0 15px;
    }
    .item-tap-one .contant-dr .item-s-result{
      box-shadow: 0px 0px 4px 1px transparent;
    }
    .item-sh{
      box-shadow: 0px 0px 4px 1px #EEE;
      margin-bottom: 15px;
    }
  }
  @media (max-width: 768px) {
    .over-title h2{
      width: 90%;
    }
    .item-s-result{
      flex-direction: column;
      box-shadow: 0px 0px 4px 1px #0000001a;
    }
    .sliders-time{
      border-left: 1px solid transparent;
      width: 100%;
    }
  }
  @media (max-width: 550px) {
    .listflex{
      flex-direction: column;
    }

    .over-title h2{
      width: 98%;
      font-size: 1.2rem;
    }
    .form-search-item{
      min-width: auto;
    }
    .container{
      width: 95%;
    }
    .slider .owl-next{
      right: 0;
    }
    .slider .owl-prev{
      left: 0;
    }
    .title-doc h3::before{
      font-size: 1.5rem;
      top: 31px;
    }
    .doc-img {

      width: 250px;
      height: 230px;}
    .grid-doc {
      width: 240px;
    }
    .list-info ul li{
      justify-content: center;
    }
    .list-info ul li span{
      font-size: .7rem;
    }
    footer .foot-head,
    .foot-bod{
      flex-direction: column;
    }
    .foot-logo{
      margin-bottom: 10px;
    }
    .nav-info {
      display: none !important;
    }
    .listflex{
      flex-direction: column;
    }
    .doc-images{
      margin: 10px auto;
    }
    .is-result-date{
      margin: 0;
      border: 0;
    }
    .is-result-info{
      text-align: center;
    }
    .is-result-info > ul{
      justify-content: center;
    }
  }
 
  .egydoc-title {
    font-size: 23px;
    font-weight: bold;
    color: $blue;
    padding-#{$start-direction}: 15px;
    position: relative;
    line-height: 1;
    margin-bottom: 0;
    white-space: normal;
    @include respond-below(sm) {
      font-size: 19px;
    }
    &:before {
      content: '';
      display: block;
      width: 3px;
      height: 100%;
      background: $pink;
      position: absolute;
      top: 0;
      #{$start-direction}: 0;
    }
  }

}
