.egydoc-form-group {
  margin-bottom: 20px;
  label {
    font-size: 18px;
    font-weight: 600;
    text-transform: capitalize;
    color: $pink;
    margin-bottom: 10px;
    display: block;
  }
  .egydoc-input {
    height: auto;
    padding: 15px 25px;
    border: 1px solid #CCDCF3;
    border-radius: 10px;
    font-size: 16px;
    color: #3E444E;
    font-weight: 500;
    resize: none;
    @include respond-below(sm) {
      padding: 15px;
    }
    &::placeholder {
      font-size: 16px;
      color: #3E444E;
      text-transform: capitalize;
      font-weight: 500;
    }
    &:focus {
      outline: none;
      box-shadow: none;
    }
  }
}