/* strat statistics*/
.statistics{
  padding: 15px;
  background: #FCFCFC;
  .title-doc h3::before {
    content: "statistics";
    top: 30px;
  }
}
.grid-doc{
  width: 300px;
  margin: 40px auto 20px auto;
  .grid-doc-item{
    display: flex;
    &:nth-child(1){
      border-bottom: 1px solid #E3E7EE;}
    .docs-item{
      padding: 15px;
      width: 50%;
      &:nth-child(1){
        border-right: 1px solid #E3E7EE;
      }
      h2{
        color: $blue;
        font-weight: 600;
        margin-bottom: 10px;
        text-align: center;
      }
      h6{
        color: #B0BED2;
        text-align: center;
      }
    }
  }
}

.doc-img{

  border-radius: 7px;
  margin: 40px auto 20px auto;
  width: 300px;
  height: 280px;


  position: relative;

  .div-doc-img{
    margin: 40px auto 20px auto;
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 10px;
    position: relative;
    z-index: 1;
    img{
      width: 100%;
    }
  }
  &::before{
    content: " ";
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    background: #E0E0E0;
    right: -12px;
    top: 12px;
    z-index: 1;
    border-radius: 10px;
  }
}
/*end statistics*/