.booking-steps-header {
  background: #FFF;
  ul {
    max-width: 670px;
    margin: auto;
    justify-content: space-between;
  }
  li {
    &:first-child a {
      padding-#{$end-direction}: 25px;
    }
    &:last-child a {
      padding-#{$start-direction}: 25px;
    }
    a {
      display: block;
      width: 100%;
      background: #FFF;
    }
  }
}
.page-step-list {
  background: #fff;
  padding: 24px 0;
  display: flex;
  justify-content: space-around;
  position: relative;
  &:before {
    content: '';
    display: block;
    width: 100%;
    height: 4px;
    background: #CFD7E4;
    @include center('both');
  }
  .page-step {
    &:first-child {
      .step-name {
        color: $blue;
      }
    }
  }
  &.step-2 {
    &:before {
      background: $blue;
    }
    &:last-child {
      .step-name {
        color: $blue;
      }
    }
  }
  .step-link {
    display: block;
    position: relative;
    @include respond-below(sm) {
      text-align: center;
    }
    span {
      display: inline-block;
      vertical-align: middle;
    }
    .step-number {
      @include circle(42px);
      background: $pink;
      color: #fff;
      font-size: 18px;
      font-weight: 600;
      @include respond-below(sm) {
        @include circle(35px);
        margin-bottom: 10px;
      }
    }
    .step-name {
      font-size: 20px;
      color: #AEB7C3;
      text-transform: capitalize;
      font-weight: 600;
      padding-#{$start-direction}: 16px;
      @include respond-below(sm) {
        font-size: 14px;
        padding-#{$start-direction}: 0;
      }
    }
  }
}