.contact-us {
  padding: 30px 0 60px;
  .page-links {
    padding: 0;
    margin-bottom: 45px;
  }
  .contact-us-box {
    padding: 40px 50px;
    background: #fff;
    border-radius: 12px;
    @include respond-below(sm) {
      padding: 30px;
    }
    .title-content {
      margin-bottom: 17px;
      img, span {
        display: inline-block;
        vertical-align: middle;
      }
      img {
        width: 35px;
        @include respond-below(sm) {
          width: 30px;
        }
      }
      span {
        font-size: 20px;
        font-weight: bold;
        padding-#{$start-direction}: 10px;
        color: $blue;
        @include respond-below(sm) {
          font-size: 18px;
        }
      }
    }
    .sub-title {
      font-size: 18px;
      color: #3E444E;
      line-height: 1;
      margin-bottom: 30px;
      @include respond-below(sm) {
        font-size: 16px;
      }
    }
    .photo-box {
      margin-bottom: 40px;
      width: 190px;
      @include respond-below(md) {
        margin: auto;
        margin-bottom: 40px;
      }
    }
    .contact-us-info-list {
      @include respond-below(sm) {
        text-align: center;
      }
      &> li {
        margin-bottom: 25px;
        &:last-child{
          margin-bottom: 0;
        }
      }
    }
    .info-label {
      font-size: 20px;
      font-weight: 600;
      margin-bottom: 0;
      color: $pink;
      line-height: 1;
      @include respond-below(sm) {
        font-size: 18px;
      }
    }
    .info-text {
      font-size: 20px;
      margin-bottom: 0;
      color: #3E444E;
      line-height: 1;
      @include respond-below(sm) {
        font-size: 16px;
      }
    }
    .social-list {
      display: flex;
      flex-wrap: wrap;
      margin-top: 10px;
      @include respond-below(sm) {
        justify-content: center;
      }
      a {
        @include circle(40px);
        background: #DAE5F4;
        color: $blue;
        margin-#{$end-direction}: 10px;
        transition: all 0.3s ease-in;
        @include respond-below(sm) {
          @include circle(35px);
          margin-#{$end-direction}: 5px;
        }
        &:hover {
          background: $blue;
          color: #fff;
        }
      }
    }
    .send-btn {
      float: $end-direction;
      max-width: 160px;
      width: 100%;
      border-radius: 14px;
      background: $pink;
      border: 0;
      padding: 15px 0;
      color: #fff;
      font-size: 20px;
      font-weight: bold;
      transition: all 0.5s ease-in;
      @include respond-below(md) {
        margin-bottom: 30px;
      }
      @include respond-below(sm) {
        max-width: 140px;
        padding: 10px 0;

      }
      &:hover {
        background: #e0318c;
      }
    }
  }
}