.booking-details {
  padding: 35px 0 55px;
  .booking-details-box {
    background: #fff;
    border-radius: 12px;
    padding: 20px 25px;
    margin-bottom: 30px;
    .patient-name {
      font-size: 18px;
      font-weight: 600;
      color: #3E444E;
      line-height: 1;
      margin-bottom: 10px;
    }
    .info-list {
      li {
        margin-bottom: 10px;
        &:last-child {
          margin-bottom: 0;
        }
      }
      .icon-box {
        flex: 0 0 20px;
        width: 20px;
      }
      .info {
        flex: 0 0 calc(100% - 20px);
        width: calc(100% - 20px);
        padding-#{$start-direction}: 10px;
        margin-bottom: 0;
        font-size: 16px;
        color: #8C9FB9;
      }
    }
  }
  .egydoc-title {
    margin-bottom: 20px;
  }
  .next-btn {
    float: $end-direction;
    display: inline-block;
    max-width: 130px;
    background: $pink;
    padding: 10px 0;
    border-radius: 14px;
    width: 100%;
    text-align: center;
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    transition: all 0.3s ease-in;
    @include respond-below(sm) {
      font-size: 16px;
    }
    &:hover {
      background: #e0318c;
    }
  }
}