.hedaer-photo{
    background: url(../images/backHome.jpg);
    background-size: cover;
    background-position: 50% center;
    background-repeat: no-repeat;
    height: 110px;
    .overlay-h{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #3333334d;
    h4{
        color: #fff;
    }
 } 
}
.details-user{
    background: #fff;
    padding: 10px 20px;
    border-radius: 6px;
    margin-top: -50px;
    .egydoc-input{
        padding: 10px 15px;
    }
    label {
        font-size: 1rem;
    }
}
.user-image{
    padding: 10px;
    text-align: center;
    img{
        width:100px;
    }
  
}
.update-profile{
    display: flex;
    flex-direction: row-reverse;
   button{
    background: #FF389F;
    border: 0;
    width: 55%;
    min-width: 150px;
    border-radius: 10px;
    text-align: center;
    color: #fff;
    outline: 0;
    cursor: pointer;
    padding: 10px 0;
    font-size: 17px;
    box-shadow: 0 7px 9px rgba(156,53,106,0.22);
    transition: all 0.5s ease-in-out;
   }
}
.line-title{
    padding: 20px 0;
    h4{
        font-size: 1.1rem;
        color: #0C50B2;
        font-weight: 600;
        position: relative;
        &::before{
            content: " ";
            display: block;
            width: 3px;
            height: 100%;
            position: absolute;
            background: #FF389F;
            #{$start-direction}: -10px;
            border-radius: 4px;
            top: 1px; 
        }
    }
}
.contant-dr{
    .is-result-img .doc-images{
        width: 120px;
        height: 110px;
    }
   
}
.ueser-but{
    border: 0;
    color: #0CB296;
    border: 1px solid #0CB296;
    padding: 5px 14px;
    font-size: 14px;
    margin: 8px 0;
    border-radius: 15px;
    background: transparent;
    transition: all 1s;
    outline: 0;
    &:hover{
        background:#0CB296;
        color: #fff;
    }
}
.body-n-tap {
    margin-bottom: 40px
}
.user-modal{
    .item-s-result{
        box-shadow: 0px 0px 4px 1px transparent;
    }
    .modal-dialog {
    max-width: 700px;
    }
    .modal-header{
        border-bottom-color:transparent;
        padding: 0 20px;
        .about-title{
            margin-bottom: 8px;
            h4{
                font-size: 1rem;

            }
            img{
                width: 19px;
            }
        }
        .item-s-result{
            margin-bottom: 0;
        }
       
    }
    .close{
        opacity: 1;
        span{
            color: #FF389F;
        }
    }
}
.section-tow{
    .about-title{
        margin-bottom: 8px;
        h4{
            font-size: 1rem;

        }
        img{
            width: 19px;
        }
    }
    p{
        font-size: .9rem;
        color: #3E444E;
    }
}
.key-valu{
    width: 230px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
    &.active{
        margin-top: 10px;
        
        span{
        color: #0C50B2;
    }

}
}