/*start  header*/
.docegy-cover{
  background: url(../images/backHome.jpg);
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
  overflow: hidden;
  .select-container {
    background: #fff;
    border-radius: 8px;
    max-width: 252px;
    position: relative;
    overflow: hidden;
    @include respond-below(lg) {
      max-width: 100%;
      margin-bottom: 10px;
    }
    width: 100%;
    .icon-box {
      @include center('vertical');
      #{$start-direction}: 20px;
      z-index: 999;
      width: 15px;
      @include respond-below(sm) {
        #{$start-direction}: 10px;
      }
    }
    .select2-container {
      width: 100% !important;

    }
    .select2-selection--single {
      padding: 17px 0;
      height: auto;
      border: 0;
      border-radius: 8px;
    }
    .select2-selection__rendered {
      color: #777;
      padding-#{$start-direction}: 50px !important;
      @include respond-below(sm) {
        padding-#{$start-direction}: 35px !important;
      }
    }
    .select2-selection__arrow {
      @include center('vertical');
      left: auto !important;
      #{$end-direction}: 10px !important;
      b {
        border-width: 9px 7px 0 7px;
        border-radius: 3px;
        border-color:$pink transparent transparent transparent;
      }
    }

  }
  .select2-container--open {
    .select2-selection__arrow b {
      border-width: 0 7px 9px  7px !important;
      border-radius: 3px;
      border-color:  transparent transparent$pink transparent !important;
    }
  }
}
.select2-results__option {
  padding: 5px 15px ;
  &:hover {
    background: $blue;
  }

}
.select2-results__option[aria-selected=true] {
  background:$pink !important;
  color: #fff;
}
.select2-search--dropdown .select2-search__field {
  height: auto;
  padding: 7px 10px;
  border-radius: 5px;
  &:focus {
    outline: none;
  }
}
nav{
  padding: 30px 0;
  position: relative;
  overflow: hidden;
  &.dark-nav {
    background: $blue;
    padding: 5px 0;
    position: relative;
  }
}
.nav-bar{
  display: flex;
  align-items: center;
  justify-content: space-between;
  .nav-logo{
    img{
      width: 120px;
    }
  }
}
.nav-info{
  display: flex;
  @include respond-below(md) {
    display: none;
  }
  .nav-link{
    ul{
      display: flex;
      align-items: center;
      li{
        margin-right: 40px;
        position: relative;
        a{
          text-transform: capitalize;
          color: #fff;
          font-weight: 600;
          letter-spacing: 2px;
          transition: all 0.5s ease-in-out;
          &:hover {
            color: #f9e1ee;
          }
        }
        &::after{
          content: " ";
          width: 2px;
          height: 25px;
          background: #ffffff54;
          display: block;
          position: absolute;
          right: -20px;
          top: 0;

        }
      }
    }
  }
}
.nav-button{
  display: flex;
  align-items: center;
  button{
    border: 1px solid #fff;
    width: 80px;
    height: 30px;
    margin-right: 20px;
    color: #fff;
    background: transparent;
    border-radius: 9px;
    outline: 0;
    cursor: pointer;
    text-transform: capitalize;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease-in-out;
    &.active{
      background: #fff;
      color: $blue;
      &:hover {

      }
    }
  }
}
.over-title{

  h2{
    margin: 30px auto;
    width: 40%;
    font-weight: 600;
    text-transform: capitalize;
    color: #fff;
    text-align: center;
    margin-top: 100px;
  }
}
.slider{
  margin-top: 20px;
  margin-bottom: 20px;
  .owl-next{
    outline: 0;
    position: absolute;
    right: -30px;
    top: 50%;
    transform: translateY(-50%);

    i{
      color: #fff;
    }
    &:hover{
      background:transparent !important;
    }
  }
  .owl-prev{
    outline: 0;
    position: absolute;
    left: -30px;
    top: 50%;
    transform: translateY(-50%);
    i{
      color: #fff;
    }
    &:hover{
      background:transparent !important;
    }
  }
}
.slid-it{
  width: 165px;
  height: 165px;
  margin: 0 auto;
  padding: 5px;
  // background: #000;
  border-radius: 8px;

  display: flex;
  align-items: center;
  justify-content: center;
  transition: all .3s;
  &:hover{
    border: 2px solid #ffffffa1;
  }
  .slid-ic{
    height: 150px;
    width: 150px;
    backdrop-filter: blur(10px);
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: 8px;
    padding: 15px;
    cursor: pointer;
    span{
      display: flex;
      justify-content: center;
      transition:all .5s;

      svg{
        width: 50px;
        fill: #fff;
        transition:all .5s;

      }
    }
    h6{
      color: #fff;
      font-weight: 600;
      text-transform: capitalize;
      text-align: center;
      margin-top: 10px;
      transition:all .5s;
    }
    &:hover{
      background: #fff;
      span{
        svg{
          fill:#FF389F;
        }
      }
      h6{
        color:$pink;
      }
    }
  }
}
.form-search{
  padding: 15px 0px;
  border-radius: 10px;
  backdrop-filter: blur(10px);
  background-color: rgba(255, 255, 255, 0.3);
  margin-top: 30px;
  margin-bottom: 20px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  .select-container{
    width: calc(100% / 4);
  }
}
.form-search-item{
  position: relative;
  background: #fff;
  //height: 40px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  width: calc(100% / 4);
  span{
    display: block;
    padding: 20px 25px;
    @include respond-below(sm) {
      padding: 20px 15px;
    }
    img{
      width: 15px;
    }
  }
  input,select{
    height: 100%;
    flex-grow: 1;
    border: 0;
    border-radius: 8px;
    outline: 0;
    color: #777;
    -webkit-appearance: none;
    appearance: none;

  }
  .search-filter-input {
    height: auto;
    padding: 20px 0;
    padding-#{$end-direction}: 30px;
    &:focus {
      box-shadow: none;
    }
  }
}
.arrows{
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  width: 15px;
}

.button-item{
  width: 16%;
  //height: 40px;
  @include respond-between(lg, xl) {
    // margin-top: 15px;
 
  }
  button{
    background:$pink;
    border: 0;
    width: 100%;
    border-radius: 10px;
    text-align: center;
    color: #fff;
    outline: 0;
    cursor: pointer;
    padding: 19px 0;
    font-size: 17px;
    box-shadow: 0 7px 9px rgba(#9C356A, 0.22);
    transition: all 0.5s ease-in-out;
    &:hover {
      background: $pink-hover;
    }
  }

}
/*animate*/
// Example 1
@keyframes scroll_1 {
  0%   { transform: translateY(0); }
  25%  { transform: translateY(-0.6em); }
  50%  { transform: translateY(0); }
  75%  { transform: translateY(0.6em); }
  100% { transform: translateY(0); }
}



.scroll-icon__wheel-outer {
  display: block;
  position: absolute;
  left: 50%;
  top: .6em;
  height: 1em;
  width: .4em;
  margin-left: -.2em;
  border-radius: .4em;
  overflow: hidden;
}

.scroll-icon__wheel-inner {
  display: block;
  height: 100%;
  width: 100%;
  border-radius: inherit;
  background: #fff;
  animation: scroll_1 2.75s ease-in-out infinite;
}

// Example 2
@keyframes scroll_2 {
  0%   { transform: translateY(0); opacity: 1; }
  25%  { opacity: 1; }
  75%  { transform: translateY(.75em); opacity: 0; }
  100% { transform: translateY(0); opacity: 0; }
}

.example--2 {
  height: 80px;
}

.scroll-icon__dot {
  display: block;
  position: absolute;
  left: 50%;
  background: #fff;
  height: .5em;
  width: .5em;
  top: .6em;
  margin-left: -.25em;
  border-radius: 50%;
  transform-origin: top center;
  backface-visibility: hidden;
  animation: scroll_2 2s ease-out infinite;
}

// General icon style
.scroll-icon {
  display: block;
  position: relative;
  height: 3em;
  width: 1.5em;
  border: .1em solid #fff;
  border-radius: 1em;
}





.page-wrap {
  display: flex;
  // height: 100vh;
}

.example {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.icon-burg{
  display: none;
  .open-menu{
    position: absolute;
    top: 23px;
    right: 20px;
    /* display: block; */
    width: 37px;
    height: 42px;
    cursor: pointer;
    z-index: 5;
    span{
      display: block;
      width: 100%;
      height: 4px;
      background-color: white;
      margin-bottom: 6px;
      -webkit-transform: rotate(-45deg);
      transform: rotate(-45deg);
      position: relative;
    }
    .open-one{
      width: 50%;
      transition: all 1s;
    }
    .open-tow{
      transition: all .5s;
    }
    .open-three{
      width: 50%;
      margin-left: 50%;
      transition: all 1s;
    }
  }
  .close-menu{
    position: absolute;
    top: 32px;
    right: 20px;
    display: block;
    width: 42px;
    height: 42px;
    cursor: pointer;

    span{
      display: block;
      width: 100%;
      height: 4px;
      background-color:$pink;
      position: relative;
    }
    .close-one{
      transform: matrix(0.70711, -0.7071, 0.7071, 0.70711, 100, -100);
      transition: all .5s;
    }
    .close-tow{

      transform: matrix(0.70711, 0.7071, -0.7071, 0.70711, -100, -100);
      top: -3px;
      transition: all .5s;
    }
  }
}
/*end header*/
.active{

  .open-one{
    transform: rotate(-45deg) translate(80px, 0px) !important  ;
  }
  .open-tow{
    transform: rotate(-45deg) translate(80px, 0px) !important  ;
  }
  .open-three{
    transform: rotate(-45deg) translate(80px, 0px) !important  ;
  }
}
.active{
  .close-one{
    transform: matrix(0.70711, -0.7071, 0.7071, 0.70711, 0, 0) !important;
  }
  .close-tow{
    transform: matrix(0.70711, 0.7071, -0.7071, 0.70711, 0, 0) !important;
  }
}

.layout{
  position: absolute;
  top: -100%;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  backdrop-filter: blur(10px);
  background-color: rgba(255, 255, 255, 0.83);
  z-index: 4;
  transition: all 1.5s;
  .layout-head{
    padding: 10px 15px;
    display: flex;
    flex-direction: row-reverse;
    span{
      img{
        width: 25px;
      }
    }
  }
}
.slid-top{
  top: 0;
}
.div-forms{
  padding: 20px;
  ul{
    text-align: center;
    li{
      a{
        text-transform: capitalize;
        color: #F5419E;
        font-weight: 600;
        letter-spacing: 2px;
      }
    }
  }
  .nav-button{
    justify-content: center;
    margin-top: 15px;
    button:nth-child(1){
      color:$blue;
      border-color:$blue !important;
    }
  }
}