.doctor-details-page {
    padding: 30px 0 90px;
    .page-links {
        margin-bottom: 35px;
        padding: 0;
    }
    nav {
        padding: 0;
    }
    .doctor-details-nav.nav-tabs {
        border-bottom: 0;
        .nav-link {
            padding: 15px 30px;
            border-top-left-radius: 12px;
            border-top-right-radius: 12px;
            border: 0;
            font-size: 18px;
            color: #929FB3;
            font-weight: 600;
            @include respond-below(sm) {
                padding: 15px 19px;
                font-size: 15px;
            }
            &.active {
                color: $blue;
                //box-shadow: 0 6px 9px rgba(#000, 0.16);
            }
        }
    }
    .doctor-details-info {
        background: #fff;
        border-radius: 0 12px 12px;
        padding: 35px 25px;
        @include respond-below(sm) {
            padding: 35px 20px;
        }
        .title-content {
            margin-bottom: 15px;
            img, span {
                display: inline-block;
                vertical-align: middle;
            }
            img {
                width: 25px;
            }
            span {
                font-size: 18px;
                font-weight: 600;
                color: #3E444E;
                padding-#{$start-direction}: 13px;
            }
        }
        .doctor-about {
            @include respond-below(md) {
                display: block !important;
            }
        }
        .info-details {
            padding-#{$start-direction}: 35px;
            width: calc(100% - 200px);
            flex: 0 0 calc(100% - 200px);
            padding-#{$end-direction}: 15px;
            font-size: 16px;
            line-height: 30px;
            color: #6F747D;
            margin-bottom: 25px;
            @include respond-below(md) {
                width: 100%;
                padding-#{$end-direction}: 0;
            }
            @include respond-below(sm) {
                padding-#{$start-direction}: 0;
            }
        }
        .overal-rating {
            flex: 0 0 200px;
            width: 200px;
            padding-#{$start-direction}: 30px;
            border-#{$start-direction}: 1px solid #D2D7E1;
            height: fit-content;
            @include respond-below(md) {
                width: 100%;
                padding-#{$start-direction}: 0;
                border-#{$start-direction}: 0;
                padding-top: 30px;
                border-top: 1px solid #d2d7e1;
                margin-bottom: 40px;

            }
            .name {
                font-size: 20px;
                font-weight: 600;
                color: $blue;
                margin-bottom: 15px;
                line-height: 1;
            }
            .rate-number {
                font-size: 54px;
                font-weight: bold;
                color: $pink;
                line-height: 1;
                margin-bottom: 10px;

            }
        }
        .doctor-place-info {
            padding-#{$start-direction}: 30px;
            @include respond-below(sm) {
                padding-#{$start-direction}: 0;
            }
            .icon-box {
                flex: 0 0 20px;
                width: 20px;
            }
            .info-p {
                flex: 0 0 calc(100% - 20px);
                width: calc(100% - 20px);
                padding-#{$start-direction}: 15px;
                font-size: 16px;
                color: #8C9FB9;
                margin-bottom: 10px;
                line-height: 26px;
                max-width: 90%;
                &.blue-color {
                    color: $blue;
                    font-weight: 600;
                }
            }
            .get-direction {
                font-size: 18px;
                color:$blue;
                font-weight: bold;
                text-decoration: underline!important;
                display: inline-block;
                margin-bottom: 45px;
            }
        }
        .tag-list {
            padding-#{$start-direction}: 30px;
            display: flex;
            flex-wrap: wrap;
            @include respond-below(sm) {
                padding-#{$start-direction}: 0;
            }
            li {
                background: $blue;
                border-radius: 9px;
                color: #fff;
                padding: 5px 15px;
                font-size: 15px;
                margin-#{$end-direction}: 15px;
                margin-bottom: 17px;
            }
        }
    }
    .doctor-details-qa {
        background: #fff;
        border-radius: 12px;
        padding: 35px 30px;
        .title-content {
            margin-bottom: 20px;
            img, span {
                display: inline-block;
                vertical-align: middle;
            }
            img {
                width: 25px;
            }
            span {
                font-size: 18px;
                font-weight: 600;
                color: #3E444E;
                padding-#{$start-direction}: 13px;
            }
        }
        .ask-question {
            float: $end-direction;
            display: inline-block;
            padding: 15px 30px;
            border-radius: 14px;
            border: 1px solid $blue;
            font-size: 18px;
            color: $blue;
            transition: all 0.5s ease-in;
            margin-bottom: 25px;
            @include respond-below(md) {
                padding: 15px 20px;
                font-size: 16px;
            }
            @include respond-below(sm) {
                float: none;
                padding: 10px 15px;
            }
            &:hover {
                background: $blue;
                color: #fff;
            }
        }
        .qa-list {
            li {
                padding-bottom: 20px;
                margin-bottom: 30px;
                border-bottom: 1px solid #DFE5EF;
                .question {
                    font-size: 20px;
                    font-weight: 600;
                    color: $blue;
                    margin-bottom: 15px;
                    line-height: 1;
                    @include respond-below(sm) {
                        font-size: 18px;
                    }
                }
                .answer {
                    font-size: 18px;
                    line-height: 25px;
                    color: #8B939F;
                    margin-bottom: 20px;
                    @include respond-below(sm) {
                        font-size: 15px;
                    }
                }
                .date {
                    margin-bottom: 0;
                    line-height: 1;
                    font-size: 14px;
                    color: #3E444E;
                    font-weight: 600;
                    @include respond-below(sm) {
                        font-size: 12px;
                    }
                }
            }
        }

    }
    .doctor-details-feedback {
        .pantients-reviews {
            background: #fff;
            border-radius: 12px;
            padding: 35px 30px;
            margin-bottom: 20px;
            .title-content {
                margin-bottom: 15px;
                img, span {
                    display: inline-block;
                    vertical-align: middle;
                }
                img {
                    width: 25px;
                }
                span {
                    font-size: 18px;
                    font-weight: 600;
                    color: #3E444E;
                    padding-#{$start-direction}: 13px;
                }
            }
            .big-stars {
                margin-bottom: 16px !important;
                img {
                    width: 30px;
                    @include respond-below(sm) {
                        width: 22px;
                    }
                }
            }
            .span-leight span{
                font-size: 20px;
                @include respond-below(sm) {
                    font-size: 18px;
                }
            }
        }
        .pantients-review-details {
            background: #fff;
            padding: 40px 30px;
            .feedback-list {
                &> li {
                    padding-bottom: 35px;
                    margin-bottom: 40px;
                    border-bottom: 1px solid #DFE5EF;
                    .feedback {
                        font-size: 18px;
                        font-weight: 500;
                        color: #8B939F;
                        margin-bottom: 18px;
                        max-width: 90%;
                        @include respond-below(sm) {
                            max-width: 100%;
                            font-size: 16px;
                        }
                    }
                    .feedback-owner {
                        font-size: 20px;
                        font-weight: bold;
                        margin-bottom: 5px;
                        color: #3E444E;
                        line-height: 1;
                        @include respond-below(sm) {
                            font-size: 18px;
                        }
                    }
                    .date {
                        font-size: 14px;
                        line-height: 1;
                        margin-bottom: 0;
                        color: #3E444E;
                        font-weight: 600;
                        @include respond-below(sm) {
                            font-size: 12px;
                        }
                    }
                }
            }
            .rating-list {
                justify-content: flex-start;
                margin-bottom: 18px !important;
            }
        }
    }
    .view-more {
        display: inline-block;
        background: $pink;
        max-width: 175px;
        width: 100%;
        padding: 17px 0;
        color: #fff;
        font-size: 18px;
        font-weight: 600;
        border-radius: 14px;
        box-shadow: 0 3px 9px rgba(#DBDBDB, 0.5);
        transition: all 0.5s ease-in;
        @include respond-below(sm) {
            padding: 10px 0;
            font-size: 16px;
        }
        &:hover {
            background: #e0318c;
        }
    }
}
.rating-list {
    display: flex;
    align-items: center;
    justify-content: center;
    li{
        margin-#{$end-direction}: 4px;
    }
}
.doctor-details-box {
    display: block;
    padding: 30px;
    margin-bottom: 30px;
    .listflex {
        @include respond-below(md) {
            display: block;

        }
    }
    .doc-images {
        width: 180px;
        height: auto;
        flex : 0 0 180px;
        @include respond-below(md) {
            margin: auto;
            margin-bottom: 20px;
        }
    }
    .is-result-info {
        flex: 0 0 calc(100% - 180px);
        width: calc(100% - 180px);
        padding-#{$start-direction}: 30px;
        @include respond-below(md) {
            width: 100%;
            padding-#{$start-direction}: 0;
            text-align: center;
        }
        .doctor-name {
            font-size: 20px;
            margin-bottom: 5px;
            font-weight: bold;
            @include respond-below(sm) {
                font-size: 18px;
            }
        }
        .doctor-experience {
            font-size: 15px;
            color: #3E444E;
            margin-bottom: 15px;
            line-height: 27px;
            font-weight: 500;
            @include respond-below(sm) {
                font-size: 13px;
            }
        }
        .list-info {
            margin-bottom: 0;
            display: block;
            li {
                margin-bottom: 15px;
                margin-top: 0;
                img {
                    width: 20px;
                    display: inline-block;
                    vertical-align: middle;
                    @include respond-below(sm) {
                        width: 15px;
                    }
                }
                span {
                    display: inline-block;
                    padding-#{$start-direction}: 12px;
                    vertical-align: middle;
                    font-size: 16px;
                    color: #8C9FB9;
                    font-weight: 600;
                    @include respond-below(sm) {
                        font-size: 14px;
                    }
                }
                &:last-child{
                    margin-bottom: 0;
                }
            }
        }
    }
}
.pick-time-box {
    @include respond-below(md) {
        margin-top: 30px;
    }
    .header {
        border-radius: 12px 12px 0 0;
        background: $pink;
        text-align: center;
        padding: 22px 0;
        color: #fff;
        text-transform: uppercase;
        font-size: 20px;
        font-weight: bold;
        @include respond-below(sm) {
            padding: 15px 0;
            font-size: 18px;
        }
    }
    .pick-time-body {
        background: #fff;
        padding: 25px ;
        padding-bottom: 10px;
        @include respond-below(sm) {
            padding: 20px;
        }
        .title-content {
            margin-bottom: 25px;
            img ,span {
                display: inline-block;
                vertical-align: middle;
            }
            img {
                font-size: 23px;
            }
            span {
                font-size: 18px;
                color: #8C9FB9;
                padding-#{$start-direction}: 20px;
                font-weight: 600;
                @include respond-below(sm) {
                    font-size: 16px;
                }
            }
        }
        .custom-checkbox-container {
            cursor: pointer;
            display: inline-block;
            margin-#{$end-direction}: 5px;
            margin-bottom: 15px;
            input {
                display: none;
            }
            .time-custome-checkbox {
                padding: 3px 8px;
                border: 1px solid #BBCBE1;
                border-radius: 5px;
                color: #3E444E;
                font-size: 14px;
            }
            input:checked + .time-custome-checkbox {
                background: $blue;
                border-color: $blue;
                color: #fff;
            }
        }
    }
    .pick-time-footer {
        display: block;
        width: 100%;
        border: 0;
        background: $blue;
        border-radius: 0 0 12px 12px;
        padding: 20px 0;
        color: #fff;
        text-transform: uppercase;
        font-size: 16px;
        font-weight: 600;
        @include respond-below(sm) {
            padding: 15px 0;
            font-size: 15px;
        }
    }
}

.book-dates-nav {
    border: 0;
    margin-bottom: 15px;
    .owl-nav {
        button {
            @include center('vertical');
            color: $blue;
            outline: none;
            &.disabled {
                cursor: default;
                i {
                    color: #DDD;
                }
            }
            i {
                position: relative;
                color: $blue;
                bottom: 3px;
            }
        }
        .owl-prev {
            #{$start-direction}: -10px;
        }
        .owl-next {
            #{$end-direction}: -10px;
        }
    }
    .nav-item {
        .nav-link {
            padding: 0 5px 10px;
            font-size: 14px;
            border: 0 !important;
            position: relative;
            text-align: center;
            white-space: nowrap;
            -webkit-transition: all 0.25s ease-in-out;
            -moz-transition: all 0.25s ease-in-out;
            -o-transition: all 0.25s ease-in-out;
            transition: all 0.25s ease-in-out;
            color: #3E444E;
            font-weight: 600;
            &:after {
                content: '';
                position: absolute;
                width: calc(100% - 10px);
                height: 3px;
                background: $blue;
                left: 5px;
                bottom: 0;
                opacity: 0;
                -webkit-transition: all 0.25s ease-in-out;
                -moz-transition: all 0.25s ease-in-out;
                -o-transition: all 0.25s ease-in-out;
                transition: all 0.25s ease-in-out;
            }
            &.active {
                color: $pink;
                &:after {
                    opacity: 1;
                }
            }
        }
    }
}