.navgation-tap {
    ul{
        display: flex;
        align-items: center;
        li{
            padding: 10px 15px;
            color: #929FB3;
            cursor: pointer;
            &.active{
                background: #fff;
                border-radius: 5px 5px 0 0;
                color: #0C50B2;

            }
        }
    }
}
.body-n-tap{
    padding: 20px;
    background: #fff;
    border-radius:0 9px 9px 9px;
}
.item-tap-one{}
.one-top-cont{
    .about-title{
        margin-top: 0;
        img{
            width: 25px;
        }
        h4{
            font-size: 1rem;
        }
    }
    p{
        font-size: .9rem;
        color: #6F747D;
    }
}
.overallrating{
    text-align: center;
    h3{
    color:#0C50B2;
    font-size: 1.2rem;
    font-weight: 600;
    text-align: center;
    margin-top: 30px;
 }
 span{
     color: #FF389F;
     font-size: 2rem;
     font-weight: bold;
 }
 ul{
     display: flex;
     align-items: center;
     justify-content: center;
     li{
         margin-#{$start-direction}:10px ;
     }
     
 }
 p{
    color: #89909D;
    font-size: .8rem;
    text-align: center;
    margin-top: 10px;
}
}