.docegy-navbar {
  position: absolute;
  top: 0;
  width: 100%;
  .nav-bar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    .nav-logo{
      img{
        width: 120px;
      }
    }
  }
  .nav-info{
    display: flex;
    .nav-link{
      ul{
        display: flex;
        align-items: center;
        li{
          margin-right: 40px;
          position: relative;
          a{
            text-transform: capitalize;
            color: #fff;
            font-weight: 600;
            letter-spacing: 2px;
          }
          &::after{
            content: " ";
            width: 2px;
            height: 25px;
            background: #ffffff54;
            display: block;
            position: absolute;
            right: -20px;
            top: 0;

          }
        }
      }
    }
  }
  .nav-button{
    display: flex;
    align-items: center;
    button{
      border: 1px solid #fff;
      width: 80px;
      height: 30px;
      display: block;
      margin-right: 20px;
      color: #fff;
      background: transparent;
      border-radius: 9px;
      outline: 0;
      cursor: pointer;
      text-transform: capitalize;
      display: flex;
      justify-content: center;
      align-items: center;
      &.active{
        background: #fff;
        color: #0C50B2;
      }
    }
  }
}
.contant-nav{
  height: 60px;
  background: #0C50B2;
  padding-top: 10px;
  position: relative;
  overflow: hidden;
}