/*start hot-docegy*/
.hot-docegy{
  padding: 15px;
  background: #FFFFFF;
}
.title-doc{
  margin-bottom: 40px;
  h3{
    text-align: center;
    color: $blue;
    font-size: 1.2rem;
    font-weight: 600;
    position: relative;
    &::before{
      content: " How To use DocEgy";
      display: block;
      font-size: 3rem;
      position: absolute;
      left: 0;
      top: 21px;
      z-index: 0;
      opacity: .1;
      width: 100%;
    }
  }
  span{
    width: 30px;
    height: 4px;
    border-radius: 2px;
    background: $pink;
    margin: 8px auto 10px auto;
    display: block;
  }
}
.doc-item{
  text-align: center;
  img{
    width: 60px;
    margin: 15px auto;
    display: block;
  }
  span{
    width: 30px;
    height: 4px;
    border-radius: 2px;
    background: $pink;
    margin: 8px auto 10px auto;
    display: block;
  }
  h6{
    text-transform: capitalize;
    color: $blue;
    font-weight: 600;
    letter-spacing: 2px;
  }
  p{
    color: $pink;
    text-align: center;
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: 600;
  }
}
/*end hot-docegy*/