.booking-checkout {
  padding: 45px 0;
  .booking-checkout-box {
    background: #fff;
    border-radius: 12px;
    padding: 20px 25px;
    margin-bottom: 25px;
    .egydoc-title {
      margin-bottom: 25px;
    }
    .payment-summary {
      padding-#{$start-direction}: 15px;
      @include respond-below(md) {
        padding-#{$start-direction}: 0;
        //text-align: center;
      }
    }
    .doctor-fees {
      margin-bottom: 10px;
      .icon-box {
        flex: 0 0 23px;
        width: 23px;
      }
      .cost {
        flex: 0 0 calc(100% - 23px);
        width: calc(100% - 23px);
        padding-#{$start-direction}: 15px;
        font-size: 18px;
        font-weight: bold;
        color: #3E444E;
        margin-bottom: 0;
      }
    }
    .photo-box {
      max-width: 215px;
      width: 100%;
      @include respond-below(md) {
        margin: auto;
        margin-bottom: 23px;
      }
    }
    .total {
      max-width: 225px;
      padding: 7px 0;
      border: 1px solid #D7DEE9;
      border-radius: 8px;
      width: 100%;
      text-align: center;
      font-size: 20px;
      font-weight: bold;
      color: $pink;
      margin-bottom: 15px;
    }
    .choose-payment {
      color: #3E444E;
      font-size: 18px;
      margin-bottom: 15px;
    }
    .payment-method-container {
      position: relative;
      cursor: pointer;
      input {
        display: none;
      }
      .custom-payment-radio {
        width: 95px;
        border: 1px solid #E7EAEE;
        border-radius: 8px;
        height: 70px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-#{$end-direction}: 9px;
        padding: 0 10px;
        @include respond-below(sm) {
          padding: 0 5px;
          width: 68px;
        }
        img {
          //width: auto;
          //height: 90px;
        }
      }
      input:checked + .custom-payment-radio {
        background: #ECF1F9;
        border-color: $blue;
      }
      &:last-child .custom-payment-radio {
        margin-#{$end-direction}: 0;
      }
    }
  }
  .pay-btn {
    float: $end-direction;
    display: inline-block;
    max-width: 130px;
    background: $pink;
    padding: 10px 0;
    border-radius: 14px;
    width: 100%;
    text-align: center;
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    transition: all 0.3s ease-in;
    @include respond-below(sm) {
      font-size: 16px;
    }
    &:hover {
      background: #e0318c;
    }
  }
}