.login-register-content {
  background: #F0F3F7;
  padding: 100px 0 50px;
  .login-register-box {
    background: #fff;
    .photo-box {
      background: url("../images/doctor-bg.jpg") no-repeat center center;
      background-size: cover;
      width: 100%;
      height: 100%;
    }
    .login-register-content {
      padding: 25px 20px 20px;
      background: #fff;
      .egydoc-title {
        margin-bottom: 35px;
      }
    }
    .forget-password {
      float: $end-direction;
      font-size: 15px;
      font-weight: bold;
      color: $pink;
      text-decoration: underline !important;
      transition: all .5s ease-in-out;
      display: block;
      margin-bottom: 20px;
      &:hover {
        color: $pink-hover;
      }
    }
    .submit-login-register {
      display: block;
      width: 100%;
      background: $pink;
      border: 0;
      padding: 15px 0;
      border-radius: 15px;
      color: #fff;
      font-size: 18px;
      font-weight: bold;
      transition: all 0.5s ease-in;
      cursor: pointer;
      margin-bottom: 20px;
      &:hover {
        background: $pink-hover;
      }
    }
    .switch-form {
      font-size: 15px;
      color: $blue;
      a {
        font-weight: 600;
        color: $pink;
        transition: all 0.5s ease-in;
        &:hover {
          color: $pink-hover;
        }
      }
    }
  }
}