
.page-links{
    padding: 15px 0 ;
    ul{
        display: flex;
        li{
            margin-#{$start-direction}:24px ;
            position: relative;
            &:last-of-type{
                a{
                    color: #3E444E;
                }
                &::after{
                    display: none;
                }
            }
            &::after{
                content: " ";
                display: block;
                background: url(../images/arrow.svg);
                background-repeat: no-repeat;
                width: 10px;
                height: 10px;
                position: absolute;
                top: 50%;
                right: -20px;
                transform: translateY(-50%);
                
            }
            a{
                color: #0C50B2;
                font-size: .9rem;
                font-weight: 500;
            }
            
        }
    }
}
// start .filter
.filter{
    margin-bottom: 20px;
    .filter-result-head{
        background: #FF389F;
        padding: 7px ;
        border-radius: 10px 10px 0 0;
        h5{
            color: #fff;
            text-align: center;
            margin-bottom: 0;
        }
    }
}
.filter-body{
    background: #fff;
    border-radius: 0 0 10px 10px;
}
.item-filter-body{
    padding: 10px;
    border-bottom: 1px solid #DFE5EF;
    h6{
        margin-bottom: 0;
        color: #0C50B2;
        font-weight: 600;
        margin-bottom: 10px;
    }
    &:last-child{
        border: 0;
        border-radius: 0 0 8px 8px;
    }
}
.item-filter-body label{
    display: flex;
    align-items: center;
    align-content: center;
    margin-bottom: 0;
    color: var(--baltoe-text-gray);
    padding-#{$start-direction}:20px;
    font-size: 14px;
    cursor: pointer;
    [type='checkbox']{
        width: 0;
        visibility: hidden;
    }
    .checkMark {
        display: inline-block;
        width: 18px;
        height: 18px;
        border-radius: 5px;
        border: 1px solid #f2f2f2;
        background-color: #f8f8f8;
        margin-#{$end-direction}: 10px;
    }
    span{
        color: #3E444E;
        font-size: 1rem;
    }
}
.item-filter-body label:not(:last-child) {
    margin-bottom: 15px;
}
.item-filter-body label input[type='checkbox']:checked+.checkMark{
    background-image:url(../images/check.png);
    background-repeat: no-repeat;
    background-position: center;
    background-color: var(--baltoe-white);
}

// end .filter
// start result-title
.result-title{
    display: flex;
    align-items: center;
    h3{
        color: #0C50B2;
        position: relative;
        font-weight: 600;
        margin-#{$end-direction}:10px;
        &::after{
            content: " ";
            background: #FF389F;
            width: 40px;
            height: 4px;
            position: absolute;
            display: block;
            border-radius: 3px;
            bottom: -5px;
            
        }

    }
    span{
        color: #979BA2;
        font-size: .9rem;
    }
}
.contant-s-result{
    margin: 20px 0;
    
}
.item-s-result{
    padding: 15px;
    background: #fff;
    border-radius: 8px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}
.listflex{
    display: flex;
}
.is-result-img{
    margin-#{$end-direction}:10px;
.doc-images{
    width: 150px;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    border-radius: 8px;
 }
}
.is-result-info{
    h4{
        color:#0C50B2;
        font-size: 1rem;
        font-weight: 600;
        margin-bottom: 0px;
    }
    & > span{
        color: #3E444E;
        font-size: .9rem;
    }
   & > ul{
        display: flex;
        align-items: center;
        li{
            margin-#{$end-direction}: 4px;
        }
    }
    
}
.span-leight{
    span{
        font-size: .8rem;
        color: #89909D;
        font-weight: 500;
    }
}
.list-info{
    ul{
        li{
            display: flex;
            align-items: center;
            margin: 8px 0;
            img{
                margin-#{$end-direction}:8px;
                width: 15px;
            }
            span{
                color: #3E444E;
                font-size: .8rem;
            }
        }
    }
}
.sliders-time{
width: 250px;
position: relative;
border-#{$start-direction}: 1px solid #C6CFDB;
.owl-nav{
    position: absolute;
    top: 0;
    right: 20px;
    button{
        outline: 0;
        margin: 0 5px;
        &.disabled{
            i{
            color: #607d8b;
            }
        }
        i{
            color: #FF389F;
        }
    }
}
}

.is-result-date{
    padding: 4px 10px;
   
    margin-#{$start-direction}: 10px;
    width: 250px;
    ul{
        display: flex;  
        flex-wrap: wrap;
        margin-top: 15px;
        li{
            padding:5px;
            color: #fff;
            background: #0C50B2;
            margin: 5px;
            border-radius: 4px;
            cursor: pointer;
            &:hover{
                background: #FF389F;
                color: #fff;
                
            }
            a{
                margin-#{$start-direction}:10px;
                color: #FF389F;
                font-weight: 700;
                text-transform: capitalize;
                text-decoration: underline !important;
            }
            &:last-child{
                background: transparent;
                position: relative;

                &::after{
                    content: " ";
                    display: block;
                    background: url(../images/arrow.svg);
                    background-repeat: no-repeat;
                    width: 10px;
                    height: 10px;
                    position: absolute;
                    top: 50%;
                    #{$start-direction}: 0px;
                    transform: translateY(-50%);
                    
                }
            }
        }

    }
}
.title-line{
    color: #3E444E;
    font-size: 1rem;
    font-weight: 600;
    position: relative;
    &::after{
        content: " ";
        background: #FF389F;
        width: 40px;
        height: 4px;
        position: absolute;
        display: block;
        border-radius: 3px;
        bottom: -8px;
        
    }

   }
     .moreload{
         display: flex;
         flex-direction: column;
         align-items: center;
         span{
             width: 50px;
             height: 50px;
             display: block;
             border: 1px solid #0C50B2;
             border-radius: 50%;
             display: flex;
             align-items: center;
             justify-content: center;
             img{
                 width: 35px;
             }
         }
         p{
                 margin-top: 5px;
    font-weight: 500;
         }
     }  
